      if (this.fire('filesAdded', files, event)) {
        each(
          files,
          function (file) {
            if ((this.opts.singleFile && this.files.length === 0) || !this.opts.singleFile) {
              this.files.push(file);
            }
          },
          this
        );
      }
      this.fire('filesSubmitted', files, event);
